    font-size: 12px;
    font-family: var(--fc-font-regular);
    margin-top: 24px;
  }
  .nav-pills-dark svg {
    float: right;
    transform: scale(0.65);
  }
  .nav-pills-dark img {
    transform: scale(0.8);
  }
  .mob-arrow {
    float: right;
    padding-top: 7.5px;
  }
  .product-showcase-mobile {
    background: rgba(235, 236, 233, 1);
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 480px;
    min-height: auto;
    padding: 30px 30px;
    margin-bottom: 20px;
    background-color: #fff;
    border: solid 1px #a2a2a2 !important;
  }
  .product-showcase-mobile .slick-dots {
    position: absolute;
    bottom: 5%;
    display: block;
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
    text-align: center;
    right: 2%;
  }
  .product-showcase-mobile .slick-dots li {
    width: 5% !important;
  }
  .product-showcase-mobile img {
    display: flex !important;
    justify-content: center;
    height: 400px;
    object-fit: contain;
  }
  .product-showcase-mobile .slick-dots li.slick-active button::before {
    opacity: 1 !important;
    color: #000 !important;
    background-color: #000 !important;
    border: none !important;
  }
  .product-showcase-mobile .slick-dots li button::before {
    font-family: "slick";
    font-size: 10px;
    line-height: 20px;
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: 20px;
    content: "•";
    text-align: center;
    opacity: 0.25;
    background-color: #fff !important;
    color: #fff !important;
    border: 0.5px solid #000 !important;
    border-radius: 50%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  .product-showcase-mobile .slick-dots li button {
    background-color: #fff !important;
    color: #fff !important;
    border: 0.5px solid #000 !important;
    border-radius: 50% !important;
    transform: scale(0.7);
  }
  .slick-initialized .slick-slide {
    margin: 0 auto !important;
  }
  .product-desc-section h1 {
    font-size: 18px !important;
  }
  .product-desc-section h2 {
    font-size: 12px !important;
  }
  .product-single-price,
  .product-single-discounted-price,
  .product-single-discounted {
    font-size: 14px !important;
  }
